	module.exports = {"data":{"markdownRemark":{"html":"<p>After we have created our <code>document ready function</code> we can now have code that will run after the page loads. This will assure that your code does not run before the HTML is rendered to avoid bugs.</p>\n<p>jQuery often selects an HTML element with a selector, then does something to that element.</p>\n<p>The following code will make the buttons have a bouncy animation on page load.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code><script>\n  $(document).ready(function() {\n    $(\"button\").addClass(\"animated bounce\");\n  });\n</script></code></pre>\n      </div>","fields":{"slug":"/certificates/target-html-elements-with-selectors-using-jquery/"},"frontmatter":{"title":"Target HTML Elements with Selectors Using jQuery"}}},"pathContext":{"slug":"/certificates/target-html-elements-with-selectors-using-jquery/"}}

